import React from 'react'
import TableCell from '@material-ui/core/TableCell';
import Box from '@material-ui/core/Box';
import Rating from '@material-ui/lab/Rating';
import AvatarGroup from 'components/avatar-group/AvatarGroup'
import Circle from '@material-ui/icons/FiberManualRecord'
import MoodIcon from '@material-ui/icons/Mood';
import MoodBadIcon from '@material-ui/icons/MoodBad';
import { format } from 'date-fns'

import { countryToFlag } from '../../../helpers/countries'
import ProgressBar from '../../progressbar'
import { CellTypes } from '../enhanced-table.types'
import { dateValidation } from '../../../helpers/date'

export interface Row {
    row: any
    editable?: boolean
    updateRow?: (row:any, cellIdx: number) => void
} 
function  renderTableCells(props: Row) {
    const { row, editable, updateRow } = props
    const keys = Object.keys(row)

    return (
        <>
        {keys.map((key, idx) => {
            const cell = row[key]
            const { label, position, type = CellTypes.text, icon, value } = cell
            const align = position === 0 ? 'left' : 'right'
            const component = 'td'
            const scope = position === 0 ? 'row' : undefined
            const padding = position === 0 ? 'none' : undefined
            let child: React.ReactElement = <span>{value}</span>
            switch (type) {
                case CellTypes.text:
                    break
                case CellTypes.progressPercentage:
                    child =  <ProgressBar value={value} />
                    break
                case CellTypes.rating:
                    const Icon = icon ? 'circle' ? 
                        <Circle fontSize="inherit"/> : 'mood' ?
                        <MoodIcon fontSize="inherit"/> : 'moodbad' ? 
                        <MoodBadIcon fontSize="inherit"/> : null
                        : null;
                    child = ( <Rating 
                        name={label} 
                        value={value} 
                        readOnly
                        icon={icon && Icon}
                    />)
                    break;
                case CellTypes.people:
                    child = <AvatarGroup avatars={value} border="circle" size="sm" />
                    break;
                case CellTypes.image:
                    child = <AvatarGroup avatars={value} border="square" size="sm" />
                    break;
                case CellTypes.flagWithISO:
                    child = (<span
                        style={{fontSize:'1.75em', marginTop:'-0.125em'}}>
                            <span>{countryToFlag(value || 'US')}</span>
                            <span style={{fontSize:'1em', marginLeft:'0.25em'}}>{value||'US'}</span>
                        </span>)
                    break;
                case CellTypes.tags:
                    child= (
                        <Box
                            display="flex"
                            flexWrap="wrap"
                            p={1}
                            m={1}
                            css={{ maxWidth: 300 }}
                        >
                            {value.map(((tag:string) => (
                                <Box pl={0.85} pr={0.85} pt={0.65} pb={0.65} mr={0.25} mb={0.25} borderRadius="42px/42px"
                                fontSize="1.25em"
                                bgcolor="grey.300">
                                    {tag}
                                </Box>
                            )))}
                        </Box>
                    )
                break
                case CellTypes.date:
                    if(value.start) dateValidation(row, value.start)
                    if(value.end) dateValidation(row, value.end)

                    child = (
                            <span
                            style={
                                {
                                    backgroundColor: '#fdab3d',
                                    color: 'white',
                                    textAlign: 'center',
                                    width: 'auto',
                                    fontSize: '0.7rem',
                                    padding: '5px 15px',
                                    borderRadius: '15px',
                                    fontWeight: 'bold',
                                    float: 'right'
                                }
                            }>
                                {value.start ? format(new Date(value.start.year, value.start.month - 1, value.start.day), 'MMM dd') : '?????'}
                                {' - '}
                                {value.end ? format(new Date(value.end.year, value.end.month - 1, value.end.day), 'MMM dd') : '?????'}
                            </span>
                    )
                    break
                default:
                    break
              }
              if (editable && updateRow) {
                return (
                    <TableCell 
                        key={key}
                        align={align} 
                        component={component} 
                        scope={scope} 
                        padding={padding}>
                        {child}
                        onClick={(e:any)=>updateRow(row, idx)}
                    </TableCell>
                  )
              }
              return (
                <TableCell 
                    key={key}
                    align={align} 
                    component={component} 
                    scope={scope} 
                    padding={padding}>
                    {child}
                </TableCell>
              )
        })}
        </>
    )
}
export default renderTableCells