import React from 'react'
import Avatar, {  AvatarProps, Size, BorderType } from '../avatar/Avatar'
import MUIAvatarGroup from '@material-ui/lab/AvatarGroup'

interface AvatarGroupProps {
    separate?: boolean
    avatars: User[]
    proportion?: boolean
    border?: BorderType
    size?: Size
}

const AvatarGroup = (props: AvatarGroupProps) => {
    const { separate, avatars, proportion, size, border } = props
    const spacing = separate ? -7 : undefined
    const renderAvatars = avatars.map(avatar=> (
            <Avatar
                user={avatar}
                border={border}
                size={size}
                proportion={proportion}
            />)
    )

    return (<MUIAvatarGroup max={3} spacing={-7}>
                {renderAvatars}
            </MUIAvatarGroup>)
}

export default AvatarGroup