export type Location = {
  lat: number
  long: number
  countryCode: string
  stateRegion?: string
  zip?: string
  city?: string
  address?: string
}
export enum CellTypes {
    attachments,
    flag,
    milestone,
    text,
    phone,
    ellipsis,
    location,
    link,
    html,
    other,
    date,
    email,
    pastDate,
    futureDate,
    dateRange,
    pastDateRange,
    futureDateRange,
    timeRange,
    datetime,
    futureDateTime,
    pastDateTime,
    progressPercentage,
    rating,
    number,
    numberRange,
    currencyAmount,
    country,
    flagWithISO,
    person,
    people,
    image,
    tags,
    label,
    labels 
  }

export interface HeadCell {
  id: string,
  disablePadding?: boolean,
  label: string,
  type?: CellTypes,
  color?: string,
  icon?: string,
  format?: string
}

export interface TableHeadTypes {
    headCells: HeadCell[],
    numSelected: any,
    onSelectAllClick: (arg1: any) => void,
    onRequestSort: (arg1: any, arg2: string) => void
    order?: "desc" | "asc",
    orderBy?: string,
    rowCount: number,
}

type TableBodyCell = string | number | string[] | { src: string; name: string; }[] | {start: {year: number, month: number, day: number} | null, end: {year: number, month: number, day: number} | null}| null
type TableBodyRow = TableBodyCell[]
export type TableBodyRows = TableBodyRow[]