import { createMuiTheme } from '@material-ui/core'
import { Breakpoint } from '@material-ui/core/styles/createBreakpoints'
import { globalState } from '../global-state'
import { makePalette, ThemeType } from './index'

import typography from './typography'

function createTheme(variant: ThemeType):any {
  let palette:any = makePalette(variant)
  const { colors } = palette
  delete palette.colors
  palette = {...palette, ...colors}
  debugger
  globalState.palette = colors
  globalState.palette.type = palette.type
  globalState.palette.contrasText = palette.contrasText
  const theme = createMuiTheme({
    spacing: 1,
  });
  return createMuiTheme(
    /**
     * @see https://material-ui.com/customization/themes/#theme-configuration-variables
     */
    {
      // @ts-ignore
      palette,
      typography,
      props: {
        MuiButtonBase: {
          disableRipple: true
        },
        MuiButton: {
            variant: 'contained'
        },
        MuiTextField: {
            variant: 'outlined'
        }
      },
      zIndex: {
        appBar: 1200,
        drawer: 1100,
      },
      overrides: {
        MuiDrawer: {
          paper: {
            background: '#18202c',
          },
        },
        MuiPaper: {
          // Name of the component ⚛️ / style sheet
          elevation0: {
            // Name of the rule
            boxShadow: '0 0 14px 0 rgba(53,64,82,.05)', // Some CSS
          },
        },
        MuiTableCell: {
          root: { borderBottom: '1px solid rgba(224, 224, 224, .5)', padding: 0 , lineHeight: 0.7},
          
        },
        MuiButton: {
          // @ts-ignore
          variant: 'contained'
        },
        MuiTextField: {
          // @ts-ignore
          variant: 'outlined'
        }
    }}
  ) as any
}

export default createTheme
