import React, { ReactNode } from 'react';
import clsx from 'clsx';

import { withStyles, WithStyles, Theme } from '@material-ui/core/styles'
import Person from '@material-ui/icons/Person';

export type Border = 'square' | 'circle' | 'rounded'
export type Size = 'xs' | 'sm' | 'md' | 'lg' | 'xl'

export interface AvatarProps  extends WithStyles<typeof styles> {
    name?: string
    imgProps?: React.ImgHTMLAttributes<HTMLImageElement>
    sizes?: string
    src?: string
    srcSet?: string
    size?: Size
    border?:  Border
    component?: React.ElementType<any>
    children: ReactNode
    className?: string
}

/**
 *
 * Demos:
 *
 */

export const styles = (theme: Theme):any => {
  /* Styles applied to the root element. */
  const { spacing, typography } = theme
  return {
    root: {
      position: 'relative',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      flexShrink: 0,
      fontFamily: typography.fontFamily,
      lineHeight: 1,
      borderRadius: '50%',
      overflow: 'hidden',
      userSelect: 'none',
    },
    /* Styles applied to the root element if not `src` or `srcSet`. */
    colorDefault: {
      color: theme.palette.background.default,
      backgroundColor:
        theme.palette.type === 'light' ? theme.palette.grey[400] : theme.palette.grey[600],
    },
    /* Styles applied to the root element if `variant="circle"`. */
    circle: {},
    /* Styles applied to the root element if `variant="rounded"`. */
    rounded: {
      borderRadius: theme.shape.borderRadius,
    },
    /* Styles applied to the root element if `variant="square"`. */
    square: {
      borderRadius: 0,
    },
    /* Styles applied to the img element if either `src` or `srcSet` is defined. */
    img: {
      width: '100%',
      height: '100%',
      textAlign: 'center',
      // Handle non-square image. The property isn't supported by IE 11.
      objectFit: 'cover',
      // Hide alt text.
      color: 'transparent',
      // Hide the image broken icon, only works on Chrome.
      textIndent: 10000,
    },
    /* Styles applied to the fallback icon */
    fallback: {
      width: '75%',
      height: '75%',
    },
    xs: {
      width: spacing(2),
      height: spacing(2)
    },
    sm: {
        width: spacing(3),
        height: theme.spacing(3)
    },
    md: {
        width: spacing(4),
        height: spacing(4)
    },
    lg: {
        width:  spacing(5),
        height: spacing(5)
    },
    xl: {
        width: spacing(6),
        height: theme.spacing(6)
    }}
}

function useLoaded({ src, srcSet }:any) {
  const [loaded, setLoaded] = React.useState<boolean|string>(false);

  React.useEffect(() => {
    if (!src && !srcSet) {
      return undefined;
    }

    setLoaded(false);

    let active = true;
    const image = new Image();
    image.src = src;
    image.srcset = srcSet;
    image.onload = () => {
      if (!active) {
        return;
      }
      setLoaded('loaded');
    };
    image.onerror = () => {
      if (!active) {
        return;
      }
      setLoaded('error');
    };

    return () => {
      active = false;
    };
  }, [src, srcSet]);

  return loaded;
}

const Avatar = React.forwardRef(function Avatar(props: AvatarProps, ref) {
  const {
    name,
    children: childrenProp,
    classes,
    className,
    component: Component = 'div',
    imgProps,
    sizes,
    src,
    srcSet,
    border = 'circle',
    size = 'md',
    ...other
  } = props;

  let children = null;

  // Use a hook instead of onError on the img element to support server-side rendering.
  const loaded = useLoaded({ src, srcSet });
  const hasImg = src || srcSet;
  const hasImgNotFailing = hasImg && loaded !== 'error';

  if (hasImgNotFailing) {
    children = (
      <img
        alt={name}
        src={src}
        srcSet={srcSet}
        className={classes.img}
        {...imgProps}
      />
    );
  } else if (childrenProp != null) {
    children = childrenProp;
  } else if (hasImg && name) {
    children = name[0];
  } else {
    children = <Person className={classes.fallback} />;
  }

  return (
    <Component
      className={clsx(
        classes.root,
        classes[border],
        classes[size],
        {
          [classes.colorDefault]: !hasImgNotFailing,
        },
        className,
      )}
      ref={ref}
      {...other}
    >
      {children}
    </Component>
  );
});

export default withStyles(styles)(Avatar)