import React, { useState } from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import Pagination from '@material-ui/lab/Pagination'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'
import Checkbox from '@material-ui/core/Checkbox'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Switch from '@material-ui/core/Switch'
// search - https://github.com/uqix/reactkit-table
// https://github.com/techniq/mui-virtualized-table
// https://github.com/bnabriss/react-mui-pagination
import DragIndicator from '@material-ui/icons/DragIndicator'
import shortId from 'shortid'

import EnhancedTableHead from './enhanced-table-head'
import EnhancedTableToolbar from './enhanced-table-toolbar'
import TableCells from './cell-renderers'
import { CellTypes, HeadCell } from './enhanced-table.types'
import useStyles from './styles'

import { getLookupId, setLaneCopyBuffer, initLaneBuffer, sortRows, getSorting } from '../../helpers/table'

enum Unit  {
  none = 0,
  currency = 1,
  weight = 2,
  volume = 3,
  measurement_wh = 4,
  measurement_whd = 5,
  time_hours = 6,
  time_days = 7
}

export enum Aggregation {
  none = 0,
  sum = 1,
  min = 2,
  max = 3,
  avg = 4.
}
export type Cell = string | number | string[] | number []

export type Row = Cell[]

export type Column = {
  id: number
  pos: number
  title: string
  width?: number
  renderer?: ()=> void
  editor?: ()=> void
  resizable?: boolean
  locked?: boolean // do not scoll
  type: CellTypes
  bounds: any[] // for range
  manyOf: boolean // accept many values
  sort?: (a:any, b:any)=>void
  validation?: ()=>void
  measure?: boolean
  unit?: Unit
  aggregations?: Aggregation[]
  defaultAggregation?: Aggregation
  aggregation?: Aggregation
  lookupValues?: Cell[]
}

export type TotalRow = {
  columns: number[] // column ids,
  compute: () => void
}

export type CellPos = {
  row: number
  col: number
}

export interface TableProps {
  domainId?: number
  projectId?: number
  boardId?: number
  laneId?: number
  collapsed?: boolean
  columns?: any[]
  rowsData: any[]
  defaultOrderBy?: string
  defaultOrder?: 'asc' | 'desc'
  editable?: boolean
  resizable?: boolean
  pinnedRows?: number[]
  showSummary?: boolean
  search?: boolean
  title?: string
  totalRows?: [TotalRow[]],
  headCells: HeadCell[]
}

export default function EnhancedTable(props: TableProps) {
  const { rowsData, defaultOrderBy = 'name', defaultOrder = 'asc', editable=false, domainId = -2, projectId = -2, boardId = -2, laneId = -2, headCells } = props;
  
  function setRowsData(...args: any) {
    if(arguments.length < headCells.length || arguments.length > headCells.length) {
      throw new Error(`Received ${arguments.length} Enhanced Table Row Parameters. Must be ${headCells.length}`);
    }
    const row: any = {}
    headCells.forEach((cell: any, index: any) => {
      row[cell.id] = {
        value: arguments[index]
      }
      for (let o in cell) {
        row[cell.id][o] = cell[o]
      }
      row[cell.id].position = index
    })
    return row
  }

  const lookupId = getLookupId({domainId, projectId, boardId, laneId})
  const classes = useStyles()
  const [rows, setRows] = useState(rowsData.map(row => setRowsData(...row)))
  const [order, setOrder] = useState(defaultOrder)
  const [orderBy, setOrderBy] = useState(defaultOrderBy)
  const [selected, setSelected] = useState<any[]>([])
  const [page, setPage] = useState(0)
  const [dense, setDense] = useState(false)
  const [rowsPerPage, setRowsPerPage] = useState(5)
  const [collapsed, setCollapased] = useState(false)

  const updateRow = (row: any, cellId: any, newVal: number | string |  boolean | null) => {
    const rowsCopy = rows;
    const rowIndex = rowsCopy.findIndex(element => element === row)
    rowsCopy[rowIndex][cellId].value = newVal;
    setRows(rowsCopy);
  }

  const handleRequestSort = (event: any, property: any) => {
    const isAsc = orderBy === property && order === 'asc'
    setOrder(isAsc ? 'desc' : 'asc')
    setOrderBy(property)
  }

  const handleSelectAllClick = (event: any) => {
    if (event.target.checked) {
      const newSelecteds = rows.map((_n,index) => index)
      // @ts-ignore
      setSelected(newSelecteds)
      return
    }
    setSelected([])
  }

  const handleClick = (event: any, index: number) => {
    const selectedIndex = selected.indexOf(index)
    let newSelected: any[] = []

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, index)
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1))
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1))
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      )
    }
    setSelected(newSelected)
  }

  const handleChangePage = (event: any, newPage: any) => {
    setPage(newPage)
  }

  const handleChangeRowsPerPage = (event: any) => {
    setRowsPerPage(parseInt(event.target.value, 10))
    setPage(0)
  }

  const handleChangeDense = (event: any) => {
    setDense(event.target.checked)
  }
  // @ts-ignore
  const isSelected = (index: number) => selected.indexOf(index) !== -1

  const emptyRows = rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage)
  const id = shortId.generate()

  return (
    <div className={classes.root}>
      <Paper className={classes.paper} elevation={0}>
        <EnhancedTableToolbar numSelected={selected.length}
          lookupId={lookupId}
          collapsed={collapsed}
          setCollapsed={setCollapased}
          editable={editable}
        />
        <TableContainer>
          <Table
            className={classes.table}
            aria-labelledby="tableTitle"
            size={dense ? 'small' : 'medium'}
            aria-label="enhanced table"
            id = {id}
          >
            <EnhancedTableHead
              classes={classes}
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              rowCount={rows.length}
              headCells={headCells}
            />
            <TableBody>
              {sortRows(rows, getSorting(order, orderBy))
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row: any, index: number) => {
                  const isItemSelected = isSelected(index)
                  const rowId = `${id}-row-${index}`

                  return (
                    <TableRow
                      hover
                      onClick={event => handleClick(event, index)}
                      role="checkbox"
                      aria-checked={isItemSelected}
                      tabIndex={-1}
                      key={index}
                      selected={isItemSelected}
                      id={rowId}
                    >
                      <TableCell padding="none">
                        <DragIndicator />
                      </TableCell>
                      <TableCell padding="none">
                        <Checkbox
                          color="primary"
                          checked={isItemSelected}
                          inputProps={{ 'aria-labelledby': rowId }}
                        />
                      </TableCell>
                      { TableCells({row})}
                    </TableRow>
                  )
                })}
              {emptyRows > 0 && (
                <TableRow style={{ height: (dense ? 20 : 30) * emptyRows }}>
                  <TableCell colSpan={6} />
                </TableRow>
              )}
            </TableBody>
          </Table>
        </TableContainer>
        <Pagination 
          page={page||1} 
          onChange={handleChangePage}
          count={5}
          color="primary"
        />
      </Paper>
      <FormControlLabel
        control={<Switch checked={dense} onChange={handleChangeDense} />}
        label="Dense padding"
      />
    </div>
  )
}
