const FULL_SCREEN = 'full_screen'
type Buffer = {
    rows: any[]
    date: number
}

type BufferLookup = {
    [key:string]: Buffer
}
interface GlobalState {
    laneBuffer: BufferLookup
    laneCopyBuffer: BufferLookup
    nextDomainId: number
    nextProjectId: number
    nextBoardId: number
    nextLaneId: number
    fullScreen: boolean,
    palette?: any
}
const fullScreen = (localStorage.getItem(FULL_SCREEN) === 'true')

export const globalState: GlobalState = {
    laneBuffer: {},
    laneCopyBuffer: {},
    nextDomainId: 2,
    nextProjectId: 2,
    nextBoardId: 2,
    nextLaneId: 2,
    fullScreen
}